












  import Vue from 'vue';
  import Tags from '@/views/Money/Tags.vue';
  import NumberPad from '@/views/Money/NumberPad.vue';
  import FormItem from '@/views/Money/FormItem.vue';
  import {Component} from 'vue-property-decorator';
  import recordTypeList from '@/constants/recordTypeList';
  import Tabs from '@/views/Money/Tabs.vue';

  @Component({
	  components: {Tabs, Tags, FormItem, NumberPad},
  })
  export default class Money extends Vue {
	  recordTypeList = recordTypeList;
	  record: RecordItem = {
		  tags: [],
		  notes: '',
		  type: '-',
		  amount: 0,
	  };
	  get recordList() {return this.$store.state.recordList;}
	  created() {this.$store.commit('fetchRecords');}
	  onUpdateNotes(value: string) {this.record.notes = value;}
	  onUpdateType(value: string) {this.record.type = value;}
	  onUpdateAmount(value: string) {this.record.amount = parseFloat(value);}
	  saveRecord() {this.$store.commit('createRecord', this.record);}
  }
